$semiwhite: rgba(239, 237, 232, 0.3);
$header-white-color: #f6f6f6;
$error: #d80027;
$success: #3cbf61;

$border-radius: 12px;
$border-input: 1px solid rgba(239, 237, 232, 0.3);
